import React, { useState, useEffect } from 'react';
import connect from '@vkontakte/vk-connect';
import {Alert, Div,Input,FormLayout, Button, Select } from '@vkontakte/vkui';
import axios from "axios";
import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';

class FormNewAddress extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
                city: '',
                street: '',
                home: '',
                entrance: '',
                level: '',
                flat: '',
                comment:''
        }

        this.addressItems = [
            // { label: 'Город', name: 'city', statusChech:'yes', minValue:5, valPls:'' },
            { label: 'Название улицы', name: 'street', statusChech:'yes', minValue:4, valPls:'' },
            { label: '№ дома', name: 'home', statusChech:'yes', minValue:0, valPls:'' },
            { label: '№ квартиры/офиса', name: 'flat', statusChech:'', minValue:0, valPls:'' },
            { label: '№ подъезда', name: 'entrance', statusChech:'', minValue:0, valPls:'' },
            { label: 'Этаж', name: 'level', statusChech:'', minValue:0, valPls:''},
            { label: 'Примечание', name: 'comment', statusChech:'', minValue:0, valPls:'Пример: Квартира сестры' }
        ];

        this.onChange = this.onChange.bind(this);
        this.openDefault = this.openDefault.bind(this);

        this.closePopout = this.closePopout.bind(this);

        if(this.props.AddressEdit){
            this.state = {
                    id:this.props.AddressEdit.id,
                    city: this.props.AddressEdit.city,
                    street: this.props.AddressEdit.street,
                    home: this.props.AddressEdit.home,
                    entrance: this.props.AddressEdit.podezd,
                    level: this.props.AddressEdit.etaj,
                    flat: this.props.AddressEdit.kvartira,
                    comment: this.props.AddressEdit.comment
                }
        }
    }

    onChange(e) {
        const { name, value } = e.currentTarget;
        // if((value === null || value === '') & name === 'city'){}else {
        //     this.setState({[name]: value});
        // }
        this.setState({[name]: value});
       // console.log(this.state);
    }


    openDefault () {
        this.props.setPopout({ popout:
                <Alert
                    actions={[{
                        title: 'Исправить',
                        autoclose: true,
                        style: 'cancel'
                    }]}
                    onClose={this.closePopout}
                >
                    <h2>Не все поля адреса заполнены</h2>
                    <p>Внимательно заполните все поля, для сохранения адреса доставки!</p>
                </Alert>
        });
    }



    saveNewAddress(){
        if(this.state.city === '' || this.state.street === '' || this.state.home === ''){
            this.openDefault();
        }else{
           // console.log('Все ок, отправлем адрес на сохранение');
            this.props.setPopout({ popout:<ScreenSpinner />});
            //Функция для отправки адреса на сервер.

            axios('https://sushi-time31.ru/api/setAddressUser&apiKey='+global.ApiKey+'',
                {
                    method: 'POST',
                    headers: { 'Content-Type': 'application/x-www-form-urlencoded' },
                    data: {
                        addressInfo:this.state,
                        idUserVK:this.props.userInfo.id
                    },
                    responseType:'text'
                })
                .then(({ data }) => {
                    if("error" in data){
                        console.log(data.error,'|',data.errorInfo);
                    }else{
                        if(data.result.result_add === true){
                            console.log('Обновляем адреса',data);
                                this.props.getAddressUserSystem();
                            this.props.setPopout({ popout:null});
                            this.props.setAddressDefault(0);
                        }else{
                            console.log('Ошибка ',data);
                        }
                    }
                })
                .catch((err) => {
                    console.log("ERROR: ---Запрос Новый Адрес---  ",err);
                });
        }
    }

    saveEditAddress(){
        if(this.state.city === '' || this.state.street === '' || this.state.home === ''){
            this.openDefault();
        }else{
           // console.log('Все ок, отправлем адрес на сохранение');
            this.props.setPopout({ popout:<ScreenSpinner />});
            //Функция для отправки адреса на сервер.

            axios('https://sushi-time31.ru/api/editAddressUser&apiKey='+global.ApiKey+'',
                {
                    method: 'POST',
                    headers: { 'Content-Type': 'application/x-www-form-urlencoded' },
                    data: {
                        addressInfo:this.state,
                        idUserVK:this.props.userInfo.id
                    },
                    responseType:'text'
                })
                .then(({ data }) => {
                    if("error" in data){
                        console.log(data.error,'|',data.errorInfo);
                    }else{
                        if(data.result.result_add === true){
                            console.log('Обновляем адреса',data);
                                this.props.getAddressUserSystem();
                            this.props.setPopout({ popout:null});
                            //this.props.setAddressDefault(0);
                        }else{
                            console.log('Ошибка ',data);
                        }
                    }
                })
                .catch((err) => {
                    console.log("ERROR: ---Запрос Новый Адрес---  ",err);
                });
        }
    }


    closePopout () {
        this.props.setPopout({ popout: null });
    }

    getCitySelect(idCity,def = null){
        if(idCity in global.CityListBind) {
            const ListCityOrigin = global.CityListBind[idCity].listCity;
            const ListCitys = Object.keys(global.CityListBind[idCity].listCity);
            const ListOption = ListCitys.map((data, key) => {
                return (<option
                    key={key}
                    value={ListCityOrigin[data].title}
                >
                    {
                        ListCityOrigin[data].title
                    }
                </option>);
            });
            return (
                ListOption
            );
        }else{
            return ("");
        }
    }

    getNulledForm(){
        return(
            <FormLayout>
                <Select placeholder="Город / Населенный пункт" onChange={this.onChange} name="city" key={"input_city"} top="Город">
                    {this.getCitySelect(1)}
                </Select>
                {this.addressItems.map(({ label, name, statusChech, minValue, valPls }) => (
                    <Input
                        type="text"
                        name={name}
                        key={"input_"+name}
                        top={label}
                        onChange={this.onChange}
                        status={
                            statusChech ?
                                this.state[name] ?
                                    this.state[name].length >=minValue  ? 'valid' : 'error'
                                    : 'error'
                                : ""
                        }
                        bottom={
                            statusChech ?
                                this.state[name] ?
                                    this.state[name].length >=minValue  ? '' : 'Введенные значение не корректно '+this.state[name].length
                                    : 'Обязательно к заполнению'
                                : ""
                        }
                        placeholder={valPls}
                    />
                ))}
                <Button size="l" style={{background:"#ff7300"}} onClick={()=>{this.saveNewAddress(); if(this.props.hideModals){this.props.hideModals()}}}>Подтвердить адрес</Button>
            </FormLayout>
        );
    }


    getEditForm(){
        return(
            <FormLayout>
                <Select placeholder="Город / Населенный пункт" onChange={this.onChange} name="city" key={"input_city"} top="Город" defaultValue={this.state.city}>
                    {this.getCitySelect(1)}
                </Select>
                {this.addressItems.map(({ label, name, statusChech, minValue, valPls }) => (
                    <Input
                        type="text"
                        name={name}
                        key={"input_"+name}
                        top={label}
                        onChange={this.onChange}
                        status={
                            statusChech ?
                                this.state[name] ?
                                    this.state[name].length >minValue  ? 'valid' : 'error'
                                    : 'error'
                                : ""
                        }
                        value={this.state[name]}
                        bottom={
                            statusChech ?
                                this.state[name] ?
                                    this.state[name].length >minValue  ? '' : 'Введенные значение не корректно'
                                    : 'Обязательно к заполнению'
                                : ""
                        }
                        placeholder={valPls}
                    />
                ))}
                <Button size="l" style={{background:"#ff7300"}} onClick={()=>{this.saveEditAddress(); if(this.props.hideModals){this.props.hideModals()}}}>Завершить редактирование</Button>
            </FormLayout>
        );
    }

    render(){
        return(
            <Div>
                {this.props.AddressEdit ? this.getEditForm() : this.getNulledForm()}
            </Div>
        );
    };
}

export default FormNewAddress;