import React from 'react';
import {Div, View, Panel, PanelHeader, Group, CellButton, HeaderButton, platform, IOS, Tooltip } from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import ProductList from "./ProductList";


function escapeHtmlDecode(text) {
    return text
        .replace(/&amp;/g, "&")
        .replace(/&lt;/g, "<")
        .replace(/&gt;/g, ">")
        .replace(/&quot;/g, "\"")
        .replace(/&#039;/g, "'");
}

class CategoryList extends React.Component {

    constructor (props) {
        super(props);
        this.state = {
            loadingPage: true,
            activePanel: 'brand',
            products: [],
            panelCategory: '',
            ActiveCategory: 0,
            productModal:[{"title":"Название товара","description":"Описание товара","typeproduct":"0","price":"1000"}]
        };
    }

    hndlClick = (aP) => {
        console.log('--- ', 'Нажали на кнопку '+aP);
        this.setState({ activePanel: aP })
    };

    setProductModal = (aP) => {
        console.log('--- ', 'Установили товар в модалку '+aP+' - - - -XTTT');
        this.setState({ productModal: aP })
    };

    setActiveCategory = (aP) => {
        console.log('--- ', 'Установили категорию '+aP);
        this.setState({ ActiveCategory: aP })
    };


    getInCarts(typeProduct,setCart){
        // console.log(setCart);
        const Buttons =
            <button onClick={() => {setCart(typeProduct);}}>
                В корзину
            </button>;

        return Buttons;
    }

    getCountButton(typeProduct,counts,setCart){
        const Buttons =
            <div className="CounterInCart">
                <div className="minus" onClick={() => {setCart(typeProduct,'-');}}>
                    -
                </div>
                {counts}
                <div className="pluse" onClick={() => {setCart(typeProduct);}}>
                    +
                </div>
            </div>;

        return Buttons;
    }

    getButtonInCartProduct(typeProduct,setCart){
        //console.log(this.props.CartProducts,'---0---');
        if(!!this.props.CartProducts){
            var ButOk = false;
            var id_CartPr = '';
            this.props.CartProducts.map((data,key)=>{
                if(data.id_Product === typeProduct.id_Product & data.type_product === typeProduct.type_product){
                    ButOk = true;
                    id_CartPr = key;
                }
            });

            if(ButOk === true){
                //console.log('Выполняется условие товар есть');
                return  this.getCountButton(typeProduct,this.props.CartProducts[id_CartPr]['count'],setCart);
            }else{
                //console.log('Выполняется условие товара нет');
                return this.getInCarts(typeProduct,setCart);
            }
        }else{
            //console.log('Выполняется условие корзина пуста, вывод просто кнопки');
            return this.getInCarts(typeProduct,setCart);
        }


    }

    componentDidMount() {
        this.setState({loading: true});
        this.setState({products:this.props.ProductsLists,loadingPage: false});
        //console.log('Передали: ',this.props.ProductsLists);
            }

    classNameDivCategory(){
        //console.log(this.props.queryParams.vk_platform);
        if(this.props.queryParams.vk_platform == "desktop_web"){
            return "GroupBlockCategory DivBlockDesctopCategoys";
        }else{
            return "GroupBlockCategory";
        }
    }



    render () {

        function ModalProductInfo(OPT){
            if(OPT.Loadings === false){
                console.log(OPT);
                return(
                    <div className="blockProduct" style={{"margin":"0","textAlign":"left","background":"#ffffff"}}>
                        <PanelHeader
                            theme="light"
                            noShadow={false}
                            left={<HeaderButton onClick={() => OPT.ClickBack('Panels_38')}>{osname === IOS ?
                                <Icon28ChevronBack/> : <Icon24Back/>}</HeaderButton>}
                            addon={<HeaderButton onClick={() => OPT.ClickBack('Panels_38')}>Назад</HeaderButton>}
                        >
                            {escapeHtmlDecode(OPT.ProductInfo[0].title)}
                        </PanelHeader>
                        <Div id={'product_type_'+OPT.ProductInfo[0].type_info}>
                            <div className="imgPrewieProduct"><img src={OPT.ProductInfo[0].img} alt={OPT.ProductInfo[0].title}/></div>
                            <div className="titleProduct">{escapeHtmlDecode(OPT.ProductInfo[0].title)} <span className="redTextP">{OPT.ProductInfo[0].type_info}</span></div>
                            <div className="DescrProduct">{escapeHtmlDecode(OPT.ProductInfo[0].description)}</div>
                            <div className="priceProductBig">{getPriceProducts(OPT.ProductInfo[0])}
                                {OPT.getButtonInCartProduct(OPT.ProductInfo[0],OPT.setCart)}
                            </div>
                        </Div>
                    </div>
                );
            }else{
                //console.log('Не загрузилось', OPT.CategoryList);
                return (
                    <div>
                    <PanelHeader
                        theme="light"
                        noShadow={false}
                        left={<HeaderButton onClick={() => OPT.ClickBack('Panels_38')}>{osname === IOS ?
                            <Icon28ChevronBack/> : <Icon24Back/>}</HeaderButton>}
                        addon={<HeaderButton onClick={() => OPT.ClickBack('Panels_38')}>Назад</HeaderButton>}
                    >Ошибка</PanelHeader>
                        <div style={{"textAlign":"center","padding":"5% 0"}}>Ошибка, вернитесь назад</div>
                    </div>
                );
            }
        }

        function getPriceProducts(typeProduct){
            if(!("price_discount" in typeProduct) || typeProduct.price_discount === false){
                return (<span className="priceProduct">
                {typeProduct.price} ₽
                </span>
                );
            }else{
                return(
                    <span className="priceProduct">
                    <s className="old_price_style" style={{fontWeight:"460",fontSize:"0.9em",marginRight:"5px"}}>{typeProduct.price_discount.old_price} ₽</s>
                        {typeProduct.price_discount.new_price} ₽
                </span>
                );
            }
        }


        function GetCategorys(OPT){
            if(OPT.Loadings === false) {
               // console.log(OPT,'-0-0-0-');
               // console.log('Загрузилось', OPT.CategoryList);
                //console.log('Товары ', OPT.CategoryList[OPT.ShowCategory].ProductList);
                return (<div style={{background:"#FFFFFF"}}>
                    <PanelHeader
                        theme="light"
                        noShadow={false}
                        left={<HeaderButton onClick={() => OPT.ClickCategory('brand')}>{osname === IOS ?
                            <Icon28ChevronBack/> : <Icon24Back/>}</HeaderButton>}
                        addon={<HeaderButton onClick={() => OPT.ClickCategory('brand')}>Назад</HeaderButton>}
                    >
                        {OPT.CategoryList[OPT.ShowCategory].Title}
                    </PanelHeader>
                    <ProductList
                        onClickProduct={OPT.ClickProduct.bind(this)}
                        productsLists={OPT.CategoryList[OPT.ShowCategory].ProductList} key={OPT.CategoryList[OPT.ShowCategory].id_Category}
                        idCategorys={OPT.ShowCategory}
                        nameCategory="sets"
                        setCart={OPT.setCart.bind(this)}
                        getButtonInCartProduct={OPT.getButtonInCartProduct.bind(this)}
                        setProductModal={OPT.setProductModal.bind(this)}
                    />
                    </div>
                );
            }else{
                //console.log('Не загрузилось', OPT.CategoryList);
                return (
                    <PanelHeader>Ошибка</PanelHeader>
                );
            }

        }

        function GetBrandPanels(OPT,Loadings){
           // console.log(OPT,' - - - категории грузим',OPT.Loadings);


            if(CategoryList.length === 0){
                return '';
            }else {
                if(OPT.Loadings === false) {
                    //ProductObject = Object.keys(OPT.CategoryList);
                    //console.log('Преобразовали',ProductObject);
                    // const ArrayBrands = Object.keys(OPT.CategoryList).map((CategoryProdcuts, key) =>
                    const ArrayBrands = Object.keys(OPT.CategoryList).map((idCategorys, key) =>

                        <Div
                            style={{"background":"linear-gradient( rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4) ), url(//sushi-time31.ru/images/API_IMG/"+OPT.CategoryList[idCategorys].imgCategory+")","backgroundSize":"100%"}}
                            className="DivCategoryBlock"
                            key={key+'_'+idCategorys}
                            onClick={
                                () => {
                                    OPT.ActiveCategory(idCategorys);
                                    OPT.ClickCategory('Panels_38')
                                }
                            }
                        >
                            {OPT.CategoryList[idCategorys].Title}
                        </Div>
                    );
                    return ArrayBrands;
                }
                else {
                    return 'Loading ...';
                }
                // return '';
            }

        }



        const osname = platform();

        return (
            <View activePanel={this.state.activePanel}>
                <Panel id="brand">
                    <PanelHeader
                        theme="light"
                    >
                        Категории
                    </PanelHeader>
                    <Group>
                        <Div className="logoBlock">
                            <img src="//sushi-time31.ru/images/API_IMG/logo_susshi_time.png"/>
                            {this.props.queryParams.vk_platform == "desktop_web"
                                ?
                                <Div className="PhoneDiv">
                                    <a>+7 (4722) 777-498</a>
                                </Div>
                                :
                                <Div className="PhoneDiv">
                                    <a href="tel:+74722777498">Позвонить</a>
                                </Div>
                            }
                        </Div>
                        <div id="SliderHeader">
                            {this.props.sliderHeader()}
                        </div>
                    </Group>
                        <Group
                            //title="Выбери категорию"
                            className={this.classNameDivCategory()} >
                        <GetBrandPanels key="BrandPanels" CategoryList={this.state.products} Loadings={this.state.loadingPage} ClickCategory={this.hndlClick.bind(this)} ActiveCategory={this.setActiveCategory.bind(this)}/>
                    </Group>
                </Panel>
                {/*{this.state.loadingPage ? <Panel id="Panels_38" />  : <Panel id="Panels_38" ><PanelHeader >Суши</PanelHeader></Panel> && <GetCategory CategoryList={this.state.products} Loadings={this.state.loadingPage} ClickCategory={this.hndlClick.bind(this)}/>}*/}
                <Panel id="Panels_38">
                    <GetCategorys
                        CategoryList={this.state.products}
                        Loadings={this.state.loadingPage}
                        ClickCategory={this.hndlClick.bind(this)}
                        ClickProduct={this.hndlClick.bind(this,'Panels_Product')}
                        setProductModal={this.setProductModal.bind(this)}
                        ShowCategory={this.state.ActiveCategory}
                        setCart={this.props.setCart.bind(this)}
                        getButtonInCartProduct={this.getButtonInCartProduct.bind(this)}
                    />
                </Panel>
                <Panel id="Panels_Product">

                    <ModalProductInfo
                        Loadings={this.state.loadingPage}
                        ClickBack={this.hndlClick.bind(this)}
                        ProductInfo={this.state.productModal}
                        setCart={this.props.setCart.bind(this)}
                        getButtonInCartProduct={this.getButtonInCartProduct.bind(this)}
                    />
                </Panel>
                {/*<Panel id="Panels_38" />*/}
                {/*<Panel id="Panels_38">*/}
                {/*    <PanelHeader*/}
                {/*        theme="light"*/}
                {/*        noShadow={true}*/}
                {/*        left={<HeaderButton onClick={() => this.hndlClick('brand')}>{osname === IOS ? <Icon28ChevronBack /> : <Icon24Back />}</HeaderButton>}*/}
                {/*        addon={<HeaderButton onClick={() => this.hndlClick('brand')}>Назад</HeaderButton>}*/}
                {/*    >*/}
                {/*        Cуши*/}
                {/*    </PanelHeader>*/}

                {/*    <ProductList productsLists={this.state.products} nameCategory="sushi" />*/}
                {/*</Panel>*/}
                {/*<Panel id="light2">*/}
                {/*    <PanelHeader*/}
                {/*        theme="light"*/}
                {/*        noShadow={true}*/}
                {/*        left={<HeaderButton onClick={() => this.hndlClick('brand')}>{osname === IOS ? <Icon28ChevronBack /> : <Icon24Back />}</HeaderButton>}*/}
                {/*        addon={<HeaderButton onClick={() => this.hndlClick('brand')}>Назад</HeaderButton>}>*/}
                {/*        Роллы*/}
                {/*    </PanelHeader>*/}
                {/*    <ProductList productsLists={this.state.products} nameCategory="roli" />*/}
                {/*</Panel>*/}
                {/*<Panel id="Panels_38">*/}
                {/*    <PanelHeader*/}
                {/*        theme="light"*/}
                {/*        noShadow={true}*/}
                {/*        left={<HeaderButton onClick={() => this.hndlClick('brand')}>{osname === IOS ? <Icon28ChevronBack /> : <Icon24Back />}</HeaderButton>}*/}
                {/*        addon={<HeaderButton onClick={() => this.hndlClick('brand')}>Назад</HeaderButton>}>*/}
                {/*        Сеты*/}
                {/*    </PanelHeader>*/}
                {/*    <ProductList productsLists={this.state.products} nameCategory="sets" idCategorys="0"/>*/}
                {/*</Panel>*/}
            </View>
        )
    }
}


export default CategoryList;